.debug(@background, @outline) {
    height: 100vh;
    position: fixed;
    width: 100%;

    >div {
        height: inherit;
        .wrapper();

        >div {
            height: inherit;
            .row-flex();

            >div {
                .col();
                .size(1);
                height: inherit;
                outline: @outline;
                background: @background;
            }
        }
    }
}

.clearfix() {
    &::after {
        display: block;
        content: "";
        clear: both;
    }
}